import { Promotion } from "../../digitalcommerce/offers/Promotion";
import { objectMap } from "../../utils";
import { AssetItemGroup } from "./AssetItemGroup";
import { PriceInfo } from "./PriceInfo";
import { SalesForceNamespace } from "../../common";

/**
 * Class that defines each asset item.
 */
export class AssetItem {
  /**
   * This function parse the asset item.
   * @param assetItemResponse
   * @return AssetItem
   */
  public static parse(assetItemResponse: any, input?: any): AssetItem {
    const assetItem = new AssetItem();
    const includeAttributes =
      input && input.includeAttributes ? input.includeAttributes : [];
    assetItem.productId =
      assetItemResponse.Product2Id && assetItemResponse.Product2Id.value;
    assetItem.id = assetItemResponse.Id ? assetItemResponse.Id.value : "";
    assetItem.name = assetItemResponse.Name;
    assetItem.description = assetItemResponse.Product2.Description;
    assetItem.lineNumber = assetItemResponse.lineNumber;
    assetItem.productCode = assetItemResponse.Product2.ProductCode;
    assetItem.catalogCodes = assetItemResponse.catalogCodes
      ? assetItemResponse.catalogCodes
      : [];
    assetItem.rootItemId =
      assetItemResponse[
        SalesForceNamespace.namespace + "__RootItemId__c"
      ].value;
    if (assetItemResponse.Quantity || assetItemResponse.quantity) {
      assetItem.quantity = assetItemResponse.Quantity
        ? assetItemResponse.Quantity.value
        : assetItemResponse.quantity.value;
    }

    if (
      assetItemResponse.attributeCategories &&
      assetItemResponse.attributeCategories.records
    ) {
      assetItem.attributes = [];
      assetItem.selectedAttributes = {};
      const attributeCategories = assetItemResponse.attributeCategories.records;
      attributeCategories.forEach(category => {
        if (category) {
          category.productAttributes &&
            category.productAttributes.records &&
            category.productAttributes.records.map(attribute => {});
          const attributes = objectMap(
            category.productAttributes.records,
            [
              "label",
              "multiselect",
              "inputType",
              "attributeId",
              "userValues",
              "values",
              "code",
              "hidden"
            ].concat(input.customFields)
          );
          attributes.map(attribute => {
            if (attribute.code === "Color") {
              const selectedColorAttribute = attribute.values.find(
                colorAttribute => colorAttribute.value === attribute.userValues
              );

              assetItem.selectedAttributes.ColorLabel = selectedColorAttribute
                ? selectedColorAttribute.label ||
                  selectedColorAttribute.defaultValue
                : attribute.userValues;
            }
            assetItem.selectedAttributes[attribute.code] = attribute.userValues;
            attribute.values = objectMap(
              attribute.values,
              [
                "value",
                "defaultValue",
                "disabled",
                "readonly",
                "defaultSelected",
                "label"
              ].concat(input.customFields)
            );
          });
          assetItem.attributes.push(attributes);
        }
      });
    } else if (assetItemResponse.attributeCategories) {
      assetItem.attributes = {};
      assetItem.selectedAttributes = {};
      for (const attributeCategories in assetItemResponse.attributeCategories) {
        if (
          assetItemResponse.attributeCategories.hasOwnProperty(
            attributeCategories
          )
        ) {
          const defaultAtrributes = [
            "value__c",
            "valuedescription__c",
            "attributedisplayname__c#label",
            "attributeRunTimeInfo.uiDisplayType",
            "attributeRunTimeInfo.values",
            "attributeRunTimeInfo.dataType",
            "attributeRunTimeInfo.default"
          ].concat(input.customFields);
          includeAttributes.map(includeAttributeKey => {
            defaultAtrributes.push(includeAttributeKey);
          });
          let attributes = [];

          if (includeAttributes.indexOf("ishidden__c") === -1) {
            // Filtering only ishidden__c = false AttributeCategory
            const nonHiddenAtrributeCategory = assetItemResponse.AttributeCategory[
              attributeCategories
            ].filter(category => !category.ishidden__c);
            attributes = objectMap(
              nonHiddenAtrributeCategory,
              defaultAtrributes
            );
          } else {
            attributes = objectMap(
              assetItemResponse.AttributeCategory[attributeCategories],
              defaultAtrributes
            );
          }

          attributes.map(attribute => {
            attribute.values = objectMap(attribute.values, [
              "value",
              "displayText"
            ]);
          });
          assetItem.attributes[attributeCategories] = attributes;
        }
      }
    }

    assetItem.productHierarchyPath = assetItemResponse.productHierarchyPath;
    assetItem.priceInfo = PriceInfo.parse(assetItemResponse, false, input);
    // parsing asset item groups
    if (assetItemResponse.childAssets) {
      assetItemResponse.childAssets.records.forEach(assetItemGroup => {
        assetItem.childAssets.push(AssetItemGroup.parse(assetItemGroup, input));
      });
    }
    if (input && input.customFields && input.customFields.length) {
      input.customFields.forEach(field => {
        if (assetItemResponse.hasOwnProperty(field)) {
          assetItem.customFields[field] = assetItemResponse[field];
        }
      });
    }
    if (assetItemResponse.promotions && assetItemResponse.promotions.records) {
      assetItem.promotions = [];
      assetItem.offerType = "Promotion";
      assetItemResponse.promotions.records.forEach(promotion => {
        const promoDetails = Promotion.parse(
          promotion,
          assetItemResponse,
          input
        );
        assetItem.promotions.push(promoDetails);
      });
    }

    return assetItem;
  }

  /**
   * unique key for each basket.
   */
  public productId: string;

  /**
   * unique ID for each offer.
   */
  public id: string;

  /**
   * name of the offer.
   */
  public name: string;
  /**
   * description of the offer.
   */
  public description: string;

  /**
   * line number of each bundle.
   */
  public lineNumber?: string;

  /**
   * action params of each offer.
   */
  public actions?: any;

  /**
   * Unique offer catalog code.
   */
  public productCode: any;

  /**
   * Quantity of each basket offer.
   */
  public quantity: number;

  /**
   * prices information for an offer.
   */
  public priceInfo?: PriceInfo;

  /**
   * heirarchy path to identify child-product from offer bundle.
   */
  public productHierarchyPath?: string;

  /**
   *  assetItems will hold all asset items.
   */
  public assetItems?: AssetItem[];

  /**
   * it holds various attribute types of an offer.
   */
  public attributes?: any;

  /**
   * it holds various selected default attributes of an offer.
   */
  public selectedAttributes?: any;

  /**
   * childAssets of the each asset item.
   */
  public childAssets?: AssetItemGroup[] = [];

  /**
   * media url's of an offer
   */
  public attachments?: any[] = [];

  /**
   * catalog code of an offer
   */
  public catalogCodes?: any[] = [];

  /**
   * custom attributes for an offer
   */
  public customFields?: any = {};

  /**
   *  promotions for an offer
   */
  public promotions?: any[];

  /**
   *  Error or warning messages from the response.
   *  Messages will be availabe when auto-rules of Excludes/Requires/Recommends setup for the products and the rules are triggered.
   */
  public messages?: string[];

  /**
   *  promotions for an offer
   */
  public offerType?: string;

  /**
   * RootItemId of a asset
   */
  public rootItemId?: string;
}
