import { SalesForceNamespace } from "../../common";

/**
 * Class that defines each offer.
 */
export class Promotion {
  public static parse(
    promotionResponse: any,
    cartItemResponse: any,
    input?: any
  ): Promotion {
    const promotion = new Promotion();
    promotion.name = promotionResponse.Name;
    promotion.promoActionType = promotionResponse.Action;
    promotion.actions = promotionResponse.actions;
    promotion.groupMaxQuantity =
      promotionResponse[SalesForceNamespace.namespace + "__MaxQuantity__c"];
    promotion.groupMinQuantity =
      promotionResponse[SalesForceNamespace.namespace + "__MinQuantity__c"];
    promotion.promoCode =
      promotionResponse[SalesForceNamespace.namespace + "__Code__c"];
    if (
      promotionResponse.Adjustments &&
      promotionResponse.Adjustments.length > 0
    ) {
      const promoAdjustment = {};
      promotionResponse.Adjustments.forEach(adjustment => {
        if (cartItemResponse.pricingLogValue) {
          const baseAdjustment = "BaseAdjustment";
          if (
            cartItemResponse.pricingLogValue.LogData.OT_STD_PRC &&
            cartItemResponse.pricingLogValue.LogData.OT_STD_PRC.length > 0
          ) {
            cartItemResponse.pricingLogValue.LogData.OT_STD_PRC.forEach(
              priceLogValue => {
                if (
                  priceLogValue.PriceListEntryId ===
                  adjustment[
                    SalesForceNamespace.namespace + "__PriceListEntryId__c"
                  ]
                ) {
                  const adjustmentOneTime = "adjustmentValue";
                  promoAdjustment[adjustmentOneTime] =
                    priceLogValue[baseAdjustment];
                }
              }
            );
          }
          if (
            cartItemResponse.pricingLogValue.LogData.REC_MNTH_STD_PRC &&
            cartItemResponse.pricingLogValue.LogData.REC_MNTH_STD_PRC.length > 0
          ) {
            cartItemResponse.pricingLogValue.LogData.REC_MNTH_STD_PRC.forEach(
              priceLogValue => {
                if (
                  priceLogValue.PriceListEntryId ===
                  adjustment[
                    SalesForceNamespace.namespace + "__PriceListEntryId__c"
                  ]
                ) {
                  const adjustmentRecurring = "adjustmentRecurring";
                  promoAdjustment[adjustmentRecurring] =
                    priceLogValue[baseAdjustment];
                }
              }
            );
          }
        }
        promotion.adjustments.push(promoAdjustment);
      });
    }
    if (input && input.customFields && input.customFields.length) {
      input.customFields.map(field => {
        if (promotionResponse.hasOwnProperty(field)) {
          promotion.customFields[field] = promotionResponse[field];
        }
      });
    }

    return promotion;
  }

  /**
   * Route url for deep linking for an offer.
   */
  public promoActionType?: string;

  /**
   * name of the offer.
   */
  public name: string;

  public actions: any[];
  /**
   * groupMinQuantity of the offer.
   */
  public groupMinQuantity?: number;

  /**
   * groupMaxQuantity of the offer.
   */
  public groupMaxQuantity?: number;

  /**
   * offercode.
   */
  public promoCode?: string;

  /**
   * adjustments for a promotion.
   */
  public adjustments?: any[] = [];

  /**
   * custom attributes for a promotion
   */
  public customFields?: any = {};

  /**
   * seoMetaData - It contains all meta data needed for SEO.
   * 1. Meta Data Page Title - Name
   * 2. Meta Data description - Description
   * 3. Meta Data Key words - Name
   */
}
