import { Promotion } from "../../digitalcommerce/offers/Promotion";
import { AssetItem } from "./AssetItem";
import { PriceInfo } from "./PriceInfo";
/**
 * Class that defines Assets.
 */

export class Assets {
  /**
   * This function parses the assets response.
   * @param assetsResponse
   * @return Assets
   */

  public static parse(assetsResponse: any, input?: any): Assets {
    const assets = new Assets();
    const assetResponse = assetsResponse;
    if (assetResponse && assetResponse.records) {
      if (assetResponse.records) {
        assetResponse.records.forEach(offerResponse => {
          const assetItem = AssetItem.parse(offerResponse, input);
          if (assetItem.promotions && assetItem.promotions.length > 0) {
            assetItem.promotions.forEach(promotion => {
              assets.assetItemsPromotionList.push(promotion);
            });
          }
          assets.assetItems.push(assetItem);
        });
      }
      if (input && input.customFields && input.customFields.length) {
        input.customFields.forEach(field => {
          if (assetResponse.hasOwnProperty(field)) {
            assets.customFields[field] = assetResponse[field];
          }
        });
      }
    }
    return assets;
  }

  /**
   * assets total prices.
   */
  public priceInfo: PriceInfo;

  /**
   *  assetItems will hold all assets items.
   */
  public assetItems?: AssetItem[] = [];

  /**
   * promotions for all asset items.
   */
  public assetItemsPromotionList?: Promotion[] = [];

  /**
   * custom attributes for an assets.
   */
  public customFields?: any = {};
}
