import { SalesForceNamespace } from "../../common";
import { Promotion } from "../../digitalcommerce/offers/Promotion";
import { objectMap } from "../../utils";
import { PriceInfo } from "./PriceInfo";

/**
 * Class that defines each asset item group.
 */
export class AssetItemGroup {
  /**
   * This function parse the asset item group bundle
   * @param assetItemGroupResponse - each product bundle
   * @return AssetItemGroup
   */
  public static parse(
    assetItemGroupResponse: any,
    input?: any
  ): AssetItemGroup {
    const assetItemGroup = new AssetItemGroup();
    const messageData =
      assetItemGroupResponse[
        SalesForceNamespace.namespace + "__CpqMessageData__c"
      ];
    assetItemGroup.name = assetItemGroupResponse.Name;
    assetItemGroup.id = assetItemGroupResponse.productId;
    assetItemGroup.lineNumber = assetItemGroupResponse.lineNumber;
    if (assetItemGroupResponse.Quantity || assetItemGroupResponse.quantity) {
      assetItemGroup.quantity = assetItemGroupResponse.Quantity
        ? assetItemGroupResponse.Quantity.value
        : assetItemGroupResponse.quantity.value;
    }
    if (
      assetItemGroupResponse.attributeCategories &&
      assetItemGroupResponse.attributeCategories.records
    ) {
      assetItemGroup.attributes = {};
      assetItemGroup.selectedAttributes = {};
      const attributeCategories =
        assetItemGroupResponse.attributeCategories.records;
      for (const category in attributeCategories) {
        if (attributeCategories.hasOwnProperty(category)) {
          const attributes = objectMap(
            attributeCategories[category].productAttributes.records,
            [
              "label",
              "multiselect",
              "inputType",
              "attributeId",
              "userValues",
              "values",
              "code",
              "hidden"
            ].concat(input.customFields)
          );
          attributes.map(attribute => {
            if (attribute.code === "Color") {
              const selectedColorAttribute = attribute.values.find(
                colorAttribute => colorAttribute.value === attribute.userValues
              );
              assetItemGroup.selectedAttributes.ColorLabel = selectedColorAttribute
                ? selectedColorAttribute.label ||
                  selectedColorAttribute.defaultValue
                : attribute.userValues;
            }
            assetItemGroup.selectedAttributes[attribute.code] =
              attribute.userValues;
            attribute.values = objectMap(
              attribute.values,
              [
                "value",
                "defaultValue",
                "disabled",
                "readonly",
                "defaultSelected",
                "label"
              ].concat(input.customFields)
            );
          });
          assetItemGroup.attributes[category] = attributes;
        }
      }
    }

    assetItemGroup.priceInfo = PriceInfo.parse(
      assetItemGroupResponse,
      false,
      input
    );

    if (
      assetItemGroupResponse.promotions &&
      assetItemGroupResponse.promotions.records
    ) {
      assetItemGroup.promotions = [];
      assetItemGroupResponse.promotions.records.forEach(promotion => {
        const promoDetails = Promotion.parse(
          promotion,
          assetItemGroupResponse,
          input
        );
        assetItemGroup.promotions.push(promoDetails);
      });
    }

    if (assetItemGroupResponse.childAssets) {
      assetItemGroup.childAssets = [];
      assetItemGroupResponse.childAssets.records.forEach(nestedChild => {
        assetItemGroup.childAssets.push(this.parse(nestedChild, input));
      });
    }

    if (input && input.customFields && input.customFields.length) {
      input.customFields.forEach(field => {
        if (assetItemGroupResponse.hasOwnProperty(field)) {
          assetItemGroup.customFields[field] = assetItemGroupResponse[field];
        }
      });
    }

    return assetItemGroup;
  }

  /**
   * product code of asset item group.
   */
  public id: string;

  /**
   * name of asset item group.
   */
  public name: string;

  /**
   * line number of asset item group.
   */
  public lineNumber?: string;

  /**
   * price information of asset item group.
   */
  public priceInfo?: PriceInfo;

  /**
   * Quantity of each offer.
   */
  public quantity?: number;

  /**
   * it holds various attribute types of an offer.
   */
  public attributes?: any;

  /**
   * it holds various selected default attributes of an offer.
   */
  public selectedAttributes?: any;

  /**
   * childAssets of the each asset item.
   */
  public childAssets?: AssetItemGroup[] = [];

  /**
   *  promotions for an offer
   */
  public promotions?: any[];

  /**
   * custom fields
   */
  public customFields?: any = {};
}
