import { SalesForceNamespace } from "../../common";
/**
 * Class that defines price information of an each offer.
 */
export class PriceInfo {
  /**
   * This function parse the offer prices information.
   * @param priceResponse
   * @return PriceInfo
   */
  public static parse(
    priceResponse: any,
    isBundleTotal?: boolean,
    input?: any
  ): PriceInfo {
    const priceInfo = new PriceInfo();
    if (isBundleTotal && priceResponse) {
      if (
        priceResponse[SalesForceNamespace.namespace + "__RecurringTotal__c"]
      ) {
        priceInfo.recurringCharge =
          priceResponse[
            SalesForceNamespace.namespace + "__RecurringTotal__c"
          ].value;
        priceInfo.oneTimeCharge =
          priceResponse[
            SalesForceNamespace.namespace + "__OneTimeTotal__c"
          ].value;
      } else {
        priceInfo.oneTimeCharge = priceResponse.EffectiveOneTimeTotal__c || 0;
        priceInfo.recurringCharge =
          priceResponse.EffectiveRecurringTotal__c || 0;
      }
    } else if (
      priceResponse &&
      priceResponse[SalesForceNamespace.namespace + "__OneTimeCharge__c"]
    ) {
      priceInfo.recurringCharge =
        priceResponse[
          SalesForceNamespace.namespace + "__RecurringCharge__c"
        ].value;
      priceInfo.oneTimeCharge =
        priceResponse[
          SalesForceNamespace.namespace + "__OneTimeCharge__c"
        ].value;
      if (priceResponse.promotions && priceResponse.promotions.records) {
        priceInfo.oneTimeChargeInfo =
          priceResponse[
            SalesForceNamespace.namespace + "__OneTimeCharge__c"
          ].originalValue;
        priceInfo.recurringChargeInfo =
          priceResponse[
            SalesForceNamespace.namespace + "__RecurringCharge__c"
          ].originalValue;
      }
    } else if (
      priceResponse &&
      !priceResponse[SalesForceNamespace.namespace + "__OneTimeCharge__c"]
    ) {
      priceInfo.oneTimeCharge = null;
      priceInfo.recurringCharge = null;
    }
    input.customFields &&
      input.customFields.map(customField => {
        if (priceResponse.hasOwnProperty(customField)) {
          priceInfo.customFields[customField] = priceResponse[customField];
        }
      });
    return priceInfo;
  }

  /**
   * one-time charge for an offer.
   */
  public oneTimeCharge?: number = 0;

  /**
   * recurring charge for an offer.
   */
  public recurringCharge?: number = 0;

  /**
   * one-time loyalty for an offer.
   */
  public oneTimeLoyalty?: number = 0;

  /**
   * recurring loyalty for an offer.
   */
  public recurringLoyalty?: number = 0;

  /**
   * custom attributes for an PriceInfo
   */
  public customFields?: any = {};
  /**
   * includes adjustmenets and overrides
   */
  public recurringChargeInfo?: any = {};

  /**
   * includes adjustmenets and overrides
   */
  public oneTimeChargeInfo?: any;
}
