import { BaseSDK, HttpMethod } from "../common";
import DataSource from "../datasource/datasource.sdk";
import { Result } from "../datasource/response/Result";
import { Logger } from "../logger";
import AccountSDKConfig from "./AccountSDKConfig";
import { UserContext } from "./common";
import { AccountInput } from "./common/AccountInput";
import { Assets } from "./asset/Assets";
import { GetAssetInput } from "./contracts/GetAssetInput";
import { GetAssetByAccountInput } from "./asset/GetAssetByAccountInput";
import { GetContractInput } from "./contracts/GetContractInput";
import { GetOfferInput } from "./offers/GetOfferInput";
import { ApexRestInput } from "../datasource/apexrest";
import { ApexRemoteInput } from "../datasource/apexremote";

/**
 * Account SDK Facade to to access account related information.
 *
 * The Account SDK is a JavaScript library that abstracts and simplifies the use of API REST calls.
 *
 * The SDK provides error checking and validates parameters before submitting them to the APIs,
 * ensuring that calls to the APIs are well formatted and complete. In some cases, the SDK invokes more than one API to complete a request.
 *
 * Using the SDK ensures that errors are detected before API calls are made, ensuring that Vlocity data structure rules are enforced and that
 * data is not written incorrectly to the Vlocity cart or to the product database.Using the SDK reduces UI development efforts by consolidating
 * common application and business logic to use the Account APIs.
 *
 * SDK components can be shared by different user interfaces,
 * such as: Customer custom UI Components (which can be based on any framework),
 * CMT Mobile App Shopping UI, Omniscript that uses Account APIs, and so on.
 *
 * @version v109.0.0
 */

export default class Account extends BaseSDK {
  /**
   * Create a AccountSDKConfig object that consists of a UserContext and a DataSourceService for login user.
   * @example
   * ```typescript
   *
   * // Instantiate accountSDKConfig for login user
   * const accountSDKConfig = VlocitySDK.account.createConfigForLoginUser(salesforceUrl, sessionToken);
   *
   * // Instantiate the SDK itself
   * const account = VlocitySDK.account.getInstance(accountSDKConfig);
   * ```
   * @param {string} salesforceUrl
   * @param {string} sessionToken
   * @return {accountSDKConfig} Configuration object to instantiate SDK.
   */
  public static createConfigForLoginUser(
    salesforceUrl: string,
    sessionToken: string
  ): AccountSDKConfig {
    const userContext = new UserContext();
    userContext.hasLogin = true;
    const datasourceForLoginUser = new DataSource({
      sessionId: sessionToken,
      url: salesforceUrl
    });
    return {
      context: userContext,
      create: false,
      datasource: datasourceForLoginUser
    };
  }

  /**
   * Get a singleton instance of this SDK.
   * @example
   * ```typescript
   *
   *
   * // Instantiate AccountSDKConfig for login user
   * const accountSDKConfig = VlocitySDK.account.createConfigForLoginUser(salesforceUrl, sessionToken);
   *
   * // Instantiate the SDK itself
   * const account = VlocitySDK.account.getInstance(accountSDKConfig);
   *
   * Note:
   * account is a Singleton object.  Calling getInstance(accountSDKConfig) repeatedly will return the same instance instantiated the first time when getInstance(accountSDKConfig) was called.
   *
   * // To instantiate a new instance of account, set create to true in the accountSDKConfig object:
   * accountSDKConfig.create = true;
   * account = VlocitySDK.account.getInstance(accountSDKConfig);
   * ```
   * @param {AccountSDKConfig} config
   * @return {Account} SDK instance.
   * @throws "Account:: getInstance: Config object must be given in argument to create an instance the first time."
   * @throws "Account::constructor: Account cannot be instantiated properly. Both datasource and user context objects have to be in Config object as argument."
   */
  public static getInstance(config: AccountSDKConfig): Account {
    // If there is no instance and no config to determine how to create the instance, throw an error.
    if (!Account.instance && !config) {
      throw new Error(
        "Account:getInstance: Config object must be provided in argument to create an instance the first time."
      );
    }

    if (!Account.instance || (config && config.create)) {
      Account.instance = new Account(config);
    }
    return Account.instance;
  }

  /**
   * Private singleton instance of this SDK. There should always be only 1 instance.
   */
  private static instance: Account;

  /**
   * The UserContext used for this SDK.
   */
  private context: UserContext;

  // keep track of basket
  private cartContextKey: string = null;

  /**
   * The datasource connection used by this SDK.
   */
  private datasource: DataSource;

  constructor(config: AccountSDKConfig) {
    super();
    if (config.datasource === null || config.context === null) {
      throw new Error(
        "Account:constructor: Account cannot be instantiated properly. Both datasource and user context objects have to be in Config object as argument."
      );
    }

    this.datasource = config.datasource;
    this.context = config.context;

    return;
  }

  /**
   * Create a default GetContractInput object.
   */
  public createGetContractsInput(): GetContractInput {
    return new GetContractInput();
  }

  /**
   * Returns a list of contracts.
   * @example
   * ```typescript
   *
   * // Instantiate the input object for getContracts to specify parameters
   * const input = account.createGetContractsInput();
   * input.accountId = "0011t00000DtbShAAJ"; // use your account Id
   * input.includeAssets = false; // set true to include assets in the response
   *
   * // Invoke GetContract API via method getContracts()
   * account
   *   .getContracts(input)
   *   .then(result => {
   *     Logger.info("vlocity get contracts rest call" + result);
   *   })
   *   .catch(error => {
   *     Logger.info("get contracts rest call failed" + error);
   *   });
   * ```
   * @param {GetContractInput} input
   * @return {Promise<any>}
   * @throws "GetContractInput::getAPIPath() must have accountId."
   */
  public async getContracts(input: GetContractInput): Promise<any> {
    const contractsResult = await this.invokeAPI(input, HttpMethod.GET);
    if (input.includeAssets) {
      // Create an array of promises
      const promises = [];
      const contractInput = this.createGetAssetsInput();
      if (contractsResult.records) {
        for (const item of contractsResult.records) {
          if (item) {
            contractInput.contractId = item.Id.value;
            promises.push(
              new Promise((resolve, reject) => {
                this.getAssets(contractInput).then(result => {
                  resolve(result);
                });
              })
            );
          }
        }
      }
      return Promise.all(promises);
    } else {
      return await contractsResult;
    }
  }

  /**
   * Method to parse asset response
   * @param {assetResponse} any
   * @param {input} any
   */
  public parseAssets(assetResponse: any, input?: any): Assets {
    const parsedResponse = Assets.parse(assetResponse, input);
    return parsedResponse;
  }

  /**
   * Create a default GetAssetInput object.
   */
  public createGetAssetsInput(): GetAssetInput {
    return new GetAssetInput();
  }

  /**
   * Returns offer.
   * @example
   * ```typescript
   *
   * // Instantiate the input object for getAssets to specify parameters
   * const input = account.createGetAssetsInput();
   * input.contractId = "80036000000EjcBAAS"; // use your contract Id
   * input.pagesize = 10; // use your desired page size
   *
   * // Invoke GetAsset API via method getAssets()
   * account
   *   .getAssets(input)
   *   .then(response => {
   *     Logger.info(
   *       "vlocity get asssets rest call" + response
   *     );
   *   })
   *   .catch(error => {
   *     Logger.info("get assets rest call failed" + error);
   *   });
   * ```
   * @param {GetAssetInput} input
   * @return {Promise<Result>}
   * @throws "GetAssetInput::getAPIPath() must have contractId."
   */
  public async getAssets(input: GetAssetInput): Promise<Result> {
    return this.invokeAPI(input, HttpMethod.GET);
  }

  /**
   * Create a default GetAssetByAccountInput object.
   */
  public createGetAssetsByAccountInput(): GetAssetByAccountInput {
    return new GetAssetByAccountInput();
  }

  /**
   * Returns offer.
   * @example
   * ```typescript
   *
   * // Instantiate the input object for getAssetsByAccount to specify parameters
   * const input = account.createGetAssetsByAccountInput();
   * input.accountId = "80036000000EjcBAAS"; // use your accountId
   * input.pagesize = 10; // use your desired page size
   *
   * // Invoke GetAsset API via method getAssetsByAccount()
   * account
   *   .getAssetsByAccount(input)
   *   .then(response => {
   *     Logger.info(
   *       "vlocity get getAssetsByAccount rest call" + response
   *     );
   *   })
   *   .catch(error => {
   *     Logger.info("get assets rest call failed" + error);
   *   });
   * ```
   * @param {GetAssetByAccountInput} input
   * @return {Promise<Result>}
   * @throws "GetAssetByAccountInput::getAPIPath() must have accountId."
   */
  public async getAssetsByAccount(input: GetAssetByAccountInput): Promise<any> {
    const response = await this.invokeAPI(input, HttpMethod.GET);
    return this.parseAssets(response, input);
  }

  /**
   * Create a default GetOfferInput object.
   */
  public createGetOffersInput(): GetOfferInput {
    return new GetOfferInput();
  }

  /**
   * Returns offers.
   * @example
   * ```typescript
   *
   * // Instantiate the input object for getOffers to specify parameters
   * const input = account.createGetOffersInput();
   * input.contextId = "0013600000QqLUZAA3"; // use your context Id
   * input.pageSize = 5; // use your desired page size
   *
   * // Invoke GetOffer API via method getOffers()
   * account
   *   .getOffers(input)
   *   .then(response => {
   *     Logger.info(
   *       "vlocity get offers rest call" + response
   *     );
   *   })
   *   .catch(error => {
   *     Logger.info("get offers rest call failed" + error);
   *   });
   * ```
   * @param {GetOfferInput} input
   * @return {Promise<Result>}
   * @throws "GetOfferInput::getAPIPath() must have contextId."
   */
  public async getOffers(input: GetOfferInput): Promise<any> {
    return this.invokeAPI(input, HttpMethod.GET);
  }

  /**
   * Invoke an action returning from previous API call.
   * @example
   * ```typescript
   *
   * // Instantiate the input object for getAssets to specify parameters
   * const input = account.createGetAssetsInput();
   * input.apiNamespace = "vlocity_cmt";
   * input.actionObj = {
   *     client: {
   *       records: [],
   *       params: {}
   *     },
   *     remote: {
   *       params: {}
   *     },
   *     rest: {
   *       params: {},
   *       method: "GET",
   *       link: "/v2/contracts/80036000000EjcBAAS/assets"
   *     };
   *
   * // Invoke GetAsset API via method getAssets()
   * account
   * .invokeAction(input)
   * .then(response => {
   *   Logger.info(
   *     "vlocity get assets rest call" + response
   *   );
   * })
   * .catch(error => {
   *   Logger.info(
   *     "get assets rest call failed" + error
   *   );
   * });
   * ```
   * @param {AccountInput} input
   * @return {Promise<any>}
   */
  private async invokeAction(input: AccountInput): Promise<any> {
    return await this.invokeAPI(input, HttpMethod[input.actionObj.rest.method]);
  }

  /**
   * Returns the response after successful api call.
   * @param {AccountInput} input
   * @return {Promise<any>}
   * @throws Error - needs to be caught
   */
  private async invokeAPI(
    input: AccountInput,
    httpMethod: HttpMethod
  ): Promise<any> {
    if (this.context.hasLogin) {
      Logger.info("invokeAPI ApexRest Path: " + input.getAPIPath());
      const apexRestInput: ApexRestInput = new ApexRestInput({
        body: input.getRequestPayload(),
        method: httpMethod,
        path: input.getAPIPath()
      });
      const apexRemoteInput: ApexRemoteInput = new ApexRemoteInput({
        className: input.remoteClass,
        inputMap: input.getInputMap(),
        methodName: input.remoteMethod
      });
      return await this.datasource
        .dual(apexRestInput, apexRemoteInput)
        .execute();
    }
  }
}
