import { SalesForceNamespace } from "../../common";
import { queryStringEncode } from "../../utils";
import { AccountInput } from "../common/AccountInput";

/**
 * Class that defines input for getting assets by account details  .
 * @extends AccountInput
 */
export class GetAssetByAccountInput extends AccountInput {
  /**
   * accountId to be used in part of the path of the API to get associated assets.
   */
  public accountId?: string;

  /**
   * pageSize as part of the query string of the API for pagination.
   */
  public pagesize?: number;

  /**
   * includes as part of the query string of the API for noContractAssets.
   */
  public includes?: string;

  /**
   * includeCatalogCodes as part of the query string of the API to fetch catalog codes.
   */
  public includeCatalogCodes?: boolean;

  /**
   * List of fields to be returned in the response.
   */
  public fields?: string[];

  /**
   * List of price fields for pricing calculation.
   */
  public priceDetailsFields?: string[];

  /**
   * Constructor function for GetAssetByAccountInput class
   */
  constructor() {
    super();
    this.remoteMethod = "getAssetsByAccount";
    this.pagesize = 10;
  }

  /**
   * Function that constructs the input map object containing all query parameters for API
   * @returns {any}
   */
  public getInputMap(): any {
    const inputMap: any = {};

    if (this.actionObj) {
      return this.actionObj.remote.params || {};
    }

    if (this.pagesize) {
      inputMap.pagesize = this.pagesize;
    }

    if (this.includes) {
      inputMap.includes = this.includes;
    }

    if (this.includeCatalogCodes) {
      inputMap.includeCatalogCodes = this.includeCatalogCodes;
    }

    if (this.priceDetailsFields) {
      inputMap.priceDetailsFields = this.priceDetailsFields;
    }

    if (this.fields) {
      inputMap.fields = this.fields.join(",");
    }

    return inputMap;
  }

  /**
   * Function that constructs the API path for ApexRest
   * Handles scenerio when namespace is null
   * @returns {string}
   */
  public getAPIPath(): string {
    const encodedQueryString = this.getEncodedQueryString();

    if (!this.accountId) {
      throw new Error(
        "GetAssetByAccountInput::getAPIPath() must have accountId."
      );
    }

    if (this.actionObj) {
      return (
        SalesForceNamespace.restAPIPrefix +
        this.actionObj.rest.link +
        encodedQueryString
      );
    } else {
      return `${SalesForceNamespace.restAPIPrefix}/v2/accounts/${
        this.accountId
      }/assets${encodedQueryString}`;
    }
  }

  /**
   * Function that constructs the Request body json for ApexRest and Rest when we are using HTTP methods of POST or PUT.
   * @returns {any}
   */
  public getRequestPayload(): any {
    return null;
  }

  /**
   * Function that constructs the query string for API
   * @returns {string}
   */
  protected getEncodedQueryString(): string {
    const fieldsToInclude = [
      "pagesize",
      "include",
      "priceDetailsFields",
      "fields",
      "includeCatalogCodes"
    ];

    const encodedQueryString = queryStringEncode(
      this.getInputMap(),
      true,
      fieldsToInclude
    );

    return encodedQueryString;
  }
}
